	module.exports = {"data":{"site":{"siteMetadata":{"title":"vvasiloud.io","subtitle":"Yet an other developer blog","copyright":"© 2018 vvasiloud.io, All rights reserved.","author":{"name":"vvasiloud","twitter":""},"disqusShortname":"vvasiloudgithubio","url":"https://vvasiloud.github.io/"}},"markdownRemark":{"id":"/home/aeon/IdeaProjects/vvasiloud.github.io/src/pages/articles/2018-02-11---Hello-Gatsby/index.md absPath of file >>> MarkdownRemark","html":"<p>Since I am more confortable writing in Javascript that Ruby decided to move from <a href=\"https://jekyllrb.com/\">Jekyll</a> to <a href=\"https://www.gatsbyjs.org/\">Gatbsy</a> for my personal\nblogging.</p>\n<p>I was kind overwellmed by broken ruby gems and weired errors, so I took the decision for the move. Of course I had to put some\neffort migrating and leaving my favorite theme <a href=\"https://mmistakes.github.io/minimal-mistakes/\">Minimal Mistakes</a> in favor of <a href=\"https://github.com/alxshelepenok/gatsby-starter-lumen\">Lumen</a>. With small customizations\nto adjust some parts I loved from the previous theme to it, and in the end I came up with something very similar :P…\nThe process was really straightforward and in the meantime I had some introduction to <a href=\"https://reactjs.org/\">React</a> and <a href=\"http://graphql.org/\">GraphQL</a> since\nI have an <a href=\"https://angular.io/\">Angular</a> background. Playing with <a href=\"https://reactjs.org/\">React</a> was easier than I thought!.</p>\n<p>I am going to miss some great features of <a href=\"https://jekyllrb.com/\">Jekyll</a> and <a href=\"https://www.ruby-lang.org/en/\">Ruby</a> but I think I can bear with it.</p>","fields":{"tagSlugs":["/tags/gatsby/"]},"frontmatter":{"title":"Moving to Gatsby","tags":["gatsby"],"date":"2018-02-11T22:12:03.284Z","description":"Hello Gatsby, Goodbye Jekyll :/ ..."}}},"pathContext":{"slug":"/posts/moving-to-gatsby"}}

